import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';


import '../../styles/navbar.css'

const useStyles = makeStyles((theme) => ({
    '@global': {
        ul: {
            margin: 0,
            padding: 0,
            listStyle: 'none',
        },
    },
    appBar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    toolbar: {
        flexWrap: 'wrap',
    },
    toolbarTitle: {
        flexGrow: 1,
    },
    link: {
        margin: theme.spacing(1, 1.5),
    },
}));

export default function Navbar({ name }) {
    const classes = useStyles();

    const [types, setTypes] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            url: 'http://worker.std-1378.ist.mospolytech.ru/api/type/'
        }).then(response => {
            setTypes(response.data)
        })
    }, [])

    const logout = async () => {
        await fetch('http://worker.std-1378.ist.mospolytech.ru/api/logout', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            credentials: 'include',
        });
    }

    let menu;

    if (name === undefined) {
        menu = (
            <Toolbar className={classes.toolbar}>
                <Typography variant="h6" color="inherit" noWrap className={classes.toolbarTitle}>
                    <Link className="nav" to='/'>WorkMaster</Link>
                </Typography>
                <Button color="primary" variant="outlined" className={classes.link}><Link className="nav" to='/login'>Войти</Link></Button>
                <Button color="primary" variant="outlined" className={classes.link}><Link className="nav" to='/register'>Зарегистрироваться</Link></Button>
            </Toolbar>
        )
    } else {
        menu = (
            <Toolbar className={classes.toolbar}>
                <Typography variant="h6" className={classes.title}>
                    <Link to='/' className="navbar-brand">WorkMaster</Link>
                </Typography>
                <nav>
                    {types.map(c => (
                        <Link className="nav" to={{ pathname: `/type/${c.id}/`, fromDashboard: false }} key={c.id}>{c.name}</Link>
                    ))}
                    <Link className="nav" to='/myposts'>Мои посты</Link>
                    <Link className="nav" to='/newpost'>Создать пост</Link>
                </nav>
                <Button color="primary" variant="outlined" className={classes.link}><Link to='/login' onClick={logout}>Выйти</Link></Button>
            </Toolbar>
        )
    }

    return (
        <AppBar color="default" elevation={0} className={classes.appBar}>
            {menu}
        </AppBar>
    );
}
Navbar.propTypes = {
    name: PropTypes.string
}